aside {
    grid-area: aside;
    display: flex;
    margin: 0 auto;
    border-right: 2px solid var(--border-color);
}

/* Nav Section */

.nav_menu_container {
    display: flex;
    flex-direction: column;
    align-self: center;
    margin: 2rem 0;
}

.nav_menu {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.logo_container {
    padding: 1.5rem 0;
}

.nav_logo i {
    font-size: 2rem;
    font-weight: 500;
    color: var(--primary-color);
    background-color: var(--secondary-color-2);
}

.nav_logo {
    font-size: 1.5rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .25rem;
    color: var(--secondary-text);
}

.nav_menu button {
    background-color: transparent;
    border: none;
}

.nav_list {
    display: none;
}

.menu_title {
    font-size: var(--main_menu_title);
    font-weight: 500;
    padding: 1rem 0;
}

.pinned_projects_menu {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.main_menu_button {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--main_menu_title);
    gap: .35rem;
}

.dashboard_menu {
    padding: 1.25rem 0;
}

.dashboard_button_item {
    display: flex;
    align-items: center;
    gap: .35rem;
    font-size: var(--main_menu_title);
}

.first_icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 25px;
    width: 25px;
    font-size: 1.25rem;
    color: var(--primary-color);
}

.second_icon {
    height: 25px;
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: var(--primary-color);
}

.todo_main_menu {
    display: flex;
    flex-direction: column;
    gap: .75rem;
}

.todo_settings_menu, .settings_menu {
    padding: var(--main_menu_title-padding);
}

.settings_menu {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.setting_menu_button {
    display: flex;
    align-items: center;
}

.setting_menu_button {
    display: flex;
    align-items: center;;
    font-size: 1.2rem;
    gap: .5rem;
}

.setting_menu_button .first_icon {
    font-size: 1.4rem;
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/asidebar.css"],"names":[],"mappings":"AAAA,kBAAkB;;AAElB;IACI,gBAAgB;IAChB,aAAa;IACb,cAAc;IACd,2CAA2C;AAC/C;;AAEA,gBAAgB;;AAEhB;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,2BAA2B;IAC3B,0CAA0C;AAC9C;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;IACX,4BAA4B;AAChC;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iCAAiC;IACjC,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,iCAAiC;IACjC,WAAW;AACf;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,iCAAiC;AACrC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,2BAA2B;AAC/B;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,iBAAiB;IACjB,UAAU;AACd;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":["/* Aside Section */\n\naside {\n    grid-area: aside;\n    display: flex;\n    margin: 0 auto;\n    border-right: 2px solid var(--border-color);\n}\n\n/* Nav Section */\n\n.nav_menu_container {\n    display: flex;\n    flex-direction: column;\n    align-self: center;\n    margin: 2rem 0;\n}\n\n.nav_menu {\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n}\n\n.logo_container {\n    padding: 1.5rem 0;\n}\n\n.nav_logo i {\n    font-size: 2rem;\n    font-weight: 500;\n    color: var(--primary-color);\n    background-color: var(--secondary-color-2);\n}\n\n.nav_logo {\n    font-size: 1.5rem;\n    font-weight: 500;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: .25rem;\n    color: var(--secondary-text);\n}\n\n.nav_menu button {\n    backgroun