___CSS_LOADER_EXPORT___.push([module.id, `dialog {
    min-height: 100vh;
    min-width: 100vw;
    z-index: 10;
    position: fixed;
    background-color: transparent;
}

dialog::backdrop {
    background-color: rgba(0, 0, 0, 0.8);
}

.form_container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;
    height: 100vh;
}

form {
    border: 1px solid #000;
    height: auto;
    width: 30rem;
    display: flex;
    flex-direction: column;
    gap: .65rem;
    padding: 1rem;
}

.close_task_form_container {
    display: flex;
    justify-content: end;
}

.close_form_button {
    border: none;
    background-color: transparent;
    font-size: 1.5rem;
    font-weight: 400;
}

.task_form_title {
    font-size: 1.5rem;
    font-weight: 400;
}

.task_title, 
.select_task_section > select {
    height: 2rem;
    width: 100%;
}

.select_priority_section > select {
    height: 2rem;
    width: 100%;
}

.date_section {
    display: flex;
    justify-content: space-between;
}

.date_label {
    display: flex;
    align-items: center;
    font-size: 1rem;
    font-weight: 400;
    gap: .25rem;
}

.date_input {
    height: 2rem;
}

textarea {
    height: 6rem;
    width: 100%;
    resize: none;
}

textarea::placeholder {
    font-family: 'Karla';
    font-size: 1%;
    font-weight: 400;
    color: #000;
    padding: .25rem 0 0 .25rem;
}

.task_control_panel {
    display: flex;
    justify-content: end;
    padding: .25rem;
    gap: 1rem;
}

.task_button {
    font-family: 'Karla';
    font-size: 1rem;
    font-weight: 400;
