.header_section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.75rem;
}

.header_main {
    display: flex;
    align-items: center;
    gap: .5rem;
}

.header_main i {
    font-size: 1.25rem;
    color: var(--primary-color);
}

.main_title {
    font-size: 1.5rem;
    font-weight: 500;
}

.create_task_button {
    font-size: 1.1rem;
    font-family: 'Karla';
    padding: .1rem;
    background-color: var(--background-color-alternate);
    color: var(--secondary-color-2);
    border: 1px solid var(--border-color);
    font-weight: 400;
    letter-spacing: .1rem;
    padding: .15rem .25rem;
    border-radius: .25rem;
}

/* SubHeader Section */ 

.subheader_section {
    display: flex;
    justify-content: space-between;
    padding: .5rem 2rem;
}

.subheader_list {
    display: flex;
    gap: .5rem;
    align-items: center;
}

.todo_control_button {
    font-size: 1rem;
    background: none;
    border: none;
}

.todo_control_button:hover {
    text-decoration: underline;
    text-decoration-color: var(--primary-color);
    text-underline-offset: .25rem;
    text-decoration-thickness: 5px;
}

.subheader_list {
    display: flex;
    justify-content: space-between;
}

.todo_header_section {
    display: flex;
    align-items: center;
}

.todo_control_button i {
    color: var(--primary-color);
}

.search_image {
    background-color: transparent;
}

.search_input_item {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .25rem;
    border: 1.5px solid #000;
    border-radius: .25rem;
}

.search_image {
    font-size: 1.35rem;
}

.search_input {
    height: 1.5rem;
    width: 14rem;
    border: none;
}

.search_input:focus {
    outline: var(--primary-color);
}

.search_input::placeholder {
    font-family: Karla;
    font-size: 1.1rem;
    color: #000;
}

/* End of SubHeader Section */ 

/* Todo Card Section */

.todo_main {
    padding: 1rem 2rem;
}

.todo_container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(8rem, 1fr));
    gap: 1rem;
    align-items: stretch;
}

.todo_card_section {
    border: 2px solid var(--border-color);
    display: flex;
    flex-direction: column;
    min-height: 48rem;
    border-radius: .25rem;
    background-color: var(--background-card-color);
}

.todo_header_section {
    border-bottom: 2px solid var(--background-color-alternate);
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 .5rem;
    height: 2.5rem;
    background-color: var(--background-color-alternate);
}

.todo_section_title {
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: .1rem;
    color: var(--secondary-color-2);
}

.delete_todo {
    font-size: 1.1rem;
    background-color: transparent;
    border: none;
    color: var(--secondary-color-2);
}

.toggle_todo_delete {
    display: flex;
    justify-content: end;
    z-index: 5;
}

.toggle_list {
    border: 2px solid var(--background-color-alternate);
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: .5rem;
    width: 8rem;
    text-align: center;
    padding: .5rem 0;
    gap: .5rem;
    border-radius: .1rem;
    background-color: var(--background-color-alternate);
    opacity: .95;
}

.toggle_item_button {
    border: none;
    font-size: 1.1rem;
    background-color: transparent;
    color: var(--background-card-color);
}`, "",{"version":3,"sources":["webpack://./src/assets/styles/main.css"],"names":[],"mappings":"AAAA,mBAAmB;;AAEnB;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,2BAA2B;AAC/B;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,oBAAoB;IACpB,cAAc;IACd,mDAAmD;IACnD,+BAA+B;IAC/B,qCAAqC;IACrC,gBAAgB;IAChB,qBAAqB;IACrB,sBAAsB;IACtB,qBAAqB;AACzB;;AAEA,sBAAsB;;AAEtB;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,UAAU;IACV,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,0BAA0B;IAC1B,2CAA2C;IAC3C,6BAA6B;IAC7B,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,wBAAwB;IACxB,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,cAAc;IACd,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,WAAW;AACf;;AAEA,6BAA6B;;AAE7B,sBAAsB;;AAEtB;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,0DAA0D;IAC1D,SAAS;IACT,oBAAoB;AACxB;;AAEA;IACI,qCAAqC;IACrC,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,qBAAqB;IACrB,8CAA8C;AAClD;;AAEA;IACI,0DAA0D;IAC1D,aAAa;IACb,8BAA8B;IAC9B,WAAW;IACX,gBAAgB;IAChB,cAAc;IACd,mDAAmD;AACvD;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,qBAAqB;IACrB,+BAA+B;AACnC;;AAEA;IACI,iBAAiB;IACjB,6BAA6B;IAC7B,YAAY;IACZ,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,oBAAoB;IACpB,UAAU;AACd;;AAEA;IACI,mDAAmD;IACnD,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,UAAU;IACV,WAAW;IACX,kBAAkB;IAClB,gBAAgB;IAChB,UAAU;IACV,oBAAoB;IACpB,mDAAmD;IACnD,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,iBAAiB;IACjB,6BAA6B;IAC7B,mCAAmC;AACvC","sourcesContent":["/* Header Section */\n\n.header_section {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 1.75rem;\n}\n\n.header_main {\n    display: flex;\n    align-items: center;\n    gap: .5rem;\n}\n\n.header_main i {\n    font-size: 1.25rem;\n    color: var(--primary-color);\n}\n\n.main_title {\n   